import React, { PureComponent } from 'react';
import { Tools } from "./components";
import { Canvas } from "./components";
import ReactDOM from "react-dom";

class App extends PureComponent {
    constructor(props){
        super(props);
        this.state={
            selectedTool:null,
            isReadyToSave:false,
            shapes:[],
            image: null,
            showImage:true
        }
    }

    componentDidMount() {
        this.setState({
            selectedTool: null,
            isReadyToSave:false,
        });
    }

    isReadyToSaveHaveHandler = (state) => {
        this.setState({isReadyToSave: state});
    };

    selectTool = (e, { name }) => {
        this.setState({selectedTool: name});
    };

    changeImage = image => {
        return new Promise(resolve => {
            this.setState({image});
            resolve(image);
        });
    };

    shape = {};
    changeShape = (shape) => {
        console.log("shape", shape)
        this.shape = shape;
    };

    saveShape = () => {
        const { isReadyToSave, shapes, selectedTool } = this.state;
        if(isReadyToSave){
            this.setState({shapes:[...shapes, {type:selectedTool, shape:this.shape}]});
        }else{
            alert("not ready to save");
        }
    };

    showImageHandler = () => {
        const { showImage } = this.state;
        this.setState({showImage:!showImage});
    };

    render() {
        const { selectedTool, shapes, image, showImage } = this.state;
        ReactDOM.render(
            <Tools
                showImageHandler={this.showImageHandler}
                selectTool={this.selectTool}
                saveShape={this.saveShape}
                selectedTool={selectedTool}
                changeImage={this.changeImage}
            />, document.getElementById('tools'));
        ReactDOM.render(
            <Canvas
                isReadyToSaveHaveHandler={this.isReadyToSaveHaveHandler}
                changeShape={this.changeShape}
                selectedTool={selectedTool}
                shapes={shapes}
                image={image}
                showImage={showImage}
            />, document.getElementById('containerForStage'));
        return null;
    }
}

export default App;
