import {Rect, Transformer} from "react-konva";
import React, {Component,createRef} from "react";

export class Rectangle extends Component{
    componentDidMount() {
        const { isSelected } = this.props;
        if (isSelected) {
            this.trRef.current.setNode(this.shapeRef.current);
            this.trRef.current.getLayer().batchDraw();
        }
    }

    render(){
        const { shapeProps, isSelected, onSelect, onChange } = this.props;
        this.shapeRef = createRef();
        this.trRef = createRef();
        console.log("shapeRef",this.shapeRef)
        console.log("trRef",this.trRef)
        return <React.Fragment>
            <Rect
                onClick={onSelect}
                ref={this.shapeRef}
                {...shapeProps}
                draggable
                onDragEnd={e => {
                    onChange({
                        ...shapeProps,
                        x: e.target.x(),
                        y: e.target.y()
                    });
                }}
                onTransformEnd={e => {
                    // transformer is changing scale of the node
                    // and NOT its width or height
                    // but in the store we have only width and height
                    // to match the data better we will reset scale on transform end
                    const node = this.shapeRef.current;
                    const scaleX = node.scaleX();
                    const scaleY = node.scaleY();

                    // we will reset it back
                    node.scaleX(1);
                    node.scaleY(1);
                    onChange({
                        ...shapeProps,
                        x: node.x(),
                        y: node.y(),
                        // set minimal value
                        width: Math.max(5, node.width() * scaleX),
                        height: Math.max(node.height() * scaleY)
                    });
                }}
            />
            {isSelected && <Transformer ref={this.trRef} />}
        </React.Fragment>
    }
}
