import React, {PureComponent} from "react";
import {Menu, Icon} from "semantic-ui-react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faRaspberryPi } from '@fortawesome/free-brands-svg-icons'

export class Tools extends PureComponent{
    state = {
        uploading: false,
        images: true
    };
    fileInput = React.createRef();

    onChange = e => {
        if(e.target.files.length===1) {
            const {changeImage} = this.props;
            this.setState({uploading: true});
            changeImage(URL.createObjectURL(e.target.files[0])).then(image => {
                this.setState({uploading: false, image})
            });
        }

        // Send it to back end to keep it.
    };

    imageUpload = () => {
        this.fileInput.current.click();
    };

    render(){
        const { showImageHandler, selectedTool, selectTool, saveShape } = this.props;
        return <Menu icon style={{border:'0px'}}>
            <Menu.Item style={{backgroundColor: '#4d3a5b', color:'#fff'}}
                       name='Tools'
                       content='Tools'
            />
            <Menu.Item
                style={selectedTool === 'polygon' ?
                    {backgroundColor: '#60a',color: '#fff'} :
                    {backgroundColor: '#fff',color: '#60a'}
                }
                name='polygon'
                content=''
                icon='gem'
                onClick={selectTool}
            />
            <Menu.Item
                style={selectedTool === 'rectangle' ?
                    {backgroundColor: '#60a',color: '#fff'} :
                    {backgroundColor: '#fff',color: '#60a'}
                }
                name='rectangle'
                content=''
                icon='square full'
                onClick={selectTool}
            />
            <Menu.Item
                style={selectedTool === 'circle' ?
                    {backgroundColor: '#60a',color: '#fff'} :
                    {backgroundColor: '#fff',color: '#60a'}
                }
                name='circle'
                content=''
                icon='circle'
                onClick={selectTool}
            />
            <Menu.Item
                style={selectedTool === 'raspberry' ?
                    {backgroundColor: '#60a',color: '#fff'} :
                    {backgroundColor: '#fff',color: '#60a'}
                }
                name='raspberry'
                content=''
                onClick={selectTool}
            >
                <FontAwesomeIcon icon={faRaspberryPi} />
            </Menu.Item>
            {
                selectedTool==='polygon' ?
                    <Menu.Item style={{backgroundColor: '#fff', color: '#4d3a5b'}}
                               name='undo'
                               content=''
                               icon='undo'
                               onClick={this.handleUndo}
                    /> : null
            }
            <Menu.Item style={{backgroundColor: '#4d3a5b', color:'#fff'}}
                       name='refresh'
                       content=''
                       icon='refresh'
                       onClick={this.handleClear}
            />
            <Menu.Item style={{backgroundColor: '#4d3a5b', color:'#fff'}}
                       name='image'
                       content=''
                       icon='image'
                       onClick={showImageHandler}
            />
            <Menu.Item style={{backgroundColor: '#4d3a5b', color:'#fff'}}
                       name='save'
                       content=''
                       icon='save'
                       onClick={saveShape}
            />
            <Menu.Item style={{backgroundColor: '#4d3a5b', color:'#fff'}}
                       name='upload'
                       content=''
                       onClick={this.imageUpload}
            >
                <Icon name='upload' />
                <input type='file' id='single' ref={this.fileInput} onChange={this.onChange} hidden />
            </Menu.Item>
        </Menu>
    }
}
